import styled from 'styled-components';

const HeadingH4 = styled.h4`
  font-family: 'main-quicksand-bold';
  font-size: 20px;
  color:'#000';
  margin-top: ${props => props.marginTop};
  margin-bottom: ${props => props.marginBottom};
  line-height: 1.1;
  text-align:center;

`;

export default HeadingH4;
